<template>
   <div>
        <div class="city-search">
        <input type="text" v-model="keyword" class="search-input" placeholder="请输入城市名或拼音">
    </div>
      <div class="search-centent" v-show="keyword">
            <ul>
                <li class="item" v-for="(city,index) in list" :key="index" @click="handleChangeCity(city.name)">
                    {{city.name}}
                </li>
            </ul>
        </div>
   </div>
</template>
<script>
export default {
    name:"CitySearch",
    data(){
        return {
            keyword:"",
            list:[],
            timer:null
        }
    },
    props:{
       cities:Object
    },
    methods:{
        handleChangeCity(city){
             this.$store.commit("changeCity",city)
             this.$router.push({path:"/"});
        }
    },
   watch:{
       keyword(){
           var arr = [];
           if(this.timer){
               clearTimeout(timer)
           }
           this.timer = setTimeout(() => {
               for(let key in this.cities){
               this.cities[key].forEach(element =>{
                   if(element.name.indexOf(this.keyword) > -1 || element.spell.indexOf(this.keyword) > -1){
                       arr.push(element)
                   }
                    })
                }       
            this.list  = arr;
           },100)

       }
   }
   
}
</script>

<style scoped>
    .city-search{
        height: 40px;
        background-color: #00bcd4;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .search-input{
        height: 30px;
        border: 1px solid #eee;
        border-radius: 5px;
        width: 90%;
        text-align: center;
        box-sizing: border-box;
    }
    .search-centent{
        position: absolute;
        background-color: #eee;
        left: 0;
        right: 0 ;
        top: 80px;
        bottom: 0;
    }
    .item{
        line-height: 35px;
        padding-left: 10px;
        background-color: #fff;
        color: #666;
    }
</style>


