<template>
    <div class="alphabet">
        <ul class="list">
            <li class="item" v-for="(item,index) in letters" :key="index" :ref="item"
            @click="handleLetter"
            @touchstart.prevent="handleTouchStart"
            @touchmove="handleTouchMove"
            @touchend="handleTouchEnd"
            >{{item}}
            </li>
        </ul>
    </div>
</template>

<script> 
export default {
    name:"CityAlphabet",
    data(){
        return {
            moveStatus:false,
            timer:null
        }
    },
    props:{
        cities:Object,
        
    },
    computed:{
        letters(){
            var arr = [];
            for(let key in this.cities){
                arr.push(key)
            }
            return arr
        }
    },
    updated(){
        this.Atop = this.$refs['A'][0].offsetTop;
    },
    methods:{
        handleLetter(e){
            var letter = e.target.innerText;
            this.$emit("letterHandle",letter)
        },
        handleTouchStart(){
            console.log("start")
            this.moveStatus = true;
        },
        handleTouchMove(e){
            console.log("move")
            if(this.moveStatus){
               if(this.timer){
                   clearTimeout(this.timer);
               }
            this.timer =setTimeout(() => {
                 var touchY = e.touches[0].clientY - 100;
                var index = Math.floor((touchY - this.Atop) /20);
                console.log(index)
                if(index >=0 && index < this.letters.length){
                    var letter = this.letters[index]
                    this.$emit("letterHandle",letter)
                }
            },500)
            }
        },
        handleTouchEnd(){
            console.log("end")
            this.moveStatus = false;
        }
        
    }
}
</script>

<style scoped>
    .alphabet{
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: absolute;
        right: 0;
        bottom: 0;
        top: 100px;
    }
    .list .item{
        line-height: 20px;
        width: 30px;
        text-align: center;
        color: #00bcd4 ;
    }
</style> 


