<template>
    <div class="weekend">
        <h2 class="tit">周末去哪儿</h2>
        <div class="content-list">
            <div class="content" v-for="(item,index) in list" :key="index">
                <img :src="item.imgUrl" alt="">
                <p class="title">{{item.title}}</p>
                <p class="desc">{{item.desc}}</p>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:"HomeWeekend",
    props:{
        list:Array
    }
}
</script>
<style scoped>
    .tit{
        line-height: 40px;
        background-color: #eee;
    }
    .content .title{
        font-size: 16px;
    }
    .desc{
        color: #ccc;
    }
    .content p{
        line-height: 25px;
    }
    .content img{
        width: 100%;
    }
</style>


