<template>
    <div class="header">
        <div class="left">
            <span class="iconfont">&#xe64e;</span>
        </div>
        <div class="search">
            <span class="iconfont">&#xe7c4;</span>
            请输入城市/景点/游玩主题
        </div>
        <div class="right">
            <router-link to="/city">
            {{this.$store.state.city}}
            <span class="iconfont">&#xe6f0;</span>
            </router-link>
        </div>
    </div>  
</template>
<script>
export default {
    name:"HomeHeader"
}
</script>

<style  scoped>
.header{
    display: flex;
    justify-content: space-between;
    height: .8rem;
    background-color: #00bcd4;
    line-height: .8rem;
    color: #fff;
}
.search{
    flex: 1;
    color: #ccc;
    background-color: #fff;
    border-radius: 5px;
    margin: .08rem;
    line-height: 32px;
    padding-left: .16rem;
}
.right a{
    color: #fff;
}
</style>


