<template>
    <div class="icon-list">
        <mt-swipe :auto="0">
            <mt-swipe-item v-for="(item,index) in  pages" :key="index">
                    <div class="icon" v-for="(icon,i) in item" :key="i">
                        <div class="icon-img">
                            <img :src="icon.imgUrl" alt="">
                        </div>
                        <div class="icon-desc">
                            {{icon.desc}}
                        </div>
                    </div>
            </mt-swipe-item>
        </mt-swipe>
    </div>
</template>
<script>
export default {
    name:"IconList",
    props:{
        list:Array
    },
    computed:{
        pages(){
            let pages = [];
            this.list.forEach((item,index) => {
                var page = Math.floor(index / 8);
                if(!pages[page]){
                    pages[page] = [];
                }
                pages[page].push(item);
            })
        return pages;
    }
    }
}
</script>
<style scoped>
.icon-list{
    height: 250px;
}
.icon{
    float: left;
    width: 25%;
    box-sizing: border-box;
    text-align: center;
    padding: 10px;
}
.icon-img img{
    width: 100%;
}
.icon-desc{
    margin-top: 10px;
} 
</style>


